import React from "react"
import TodoItem from "./TodoItem"
import todoData from "./todoData"

class App extends React.Component{
    constructor(){
        super()
         this.state = {
            todos: todoData,
            newItem: "",
            // cnt: todoData.length
        }
        this.handleChange = this.handleChange.bind(this)
        this.handleText = this.handleText.bind(this)
        this.btnClick = this.btnClick.bind(this)
        this.handleDel = this.handleDel.bind(this)
    }

    handleChange(id){
        this.setState(prevState => {
            const updatedTodos = prevState.todos.map(todo => {
                if (todo.id === id){
                    todo.completed = !todo.completed
                }
                return todo
            })
            return ({
                todos: updatedTodos
            })
        })
    }

    handleText(event){
        this.setState({
            newItem: event.target.value
        })
        // console.log(this.state.newItem)
    }
    
    btnClick(){
        // console.log('clicked')
        var td = this.state.todos
        // var i = this.state.cnt + 1
        td.push({
            id: td.length + 1,
            text: this.state.newItem,
            completed: false
        })
        this.setState({
            todos: td,
            // cnt: i+1
        })

    }

    handleDel(id){
        delete todoData[id-1]
        this.setState({
            todos: todoData
        })
    }

    render(){
        const TodoItems = this.state.todos.map(item => <TodoItem key={item.id} items={item} handleChange={this.handleChange} handleDel={this.handleDel}/>)
        // console.log({TodoItems})

        var date = new Date()
        var hours = date.getHours()
        var msg = ''
        if(hours<12){
            msg = 'Good Morning'
        }
        else if(hours<17){
            msg ='Good Afternoon'
        }
        else{
            msg = 'Good Evening'
        }
        return (
            <div className='todo-list'>
             <h1>React To Do List</h1>
             <h2>{msg}</h2>
             <input type='text' onChange={this.handleText}/>
             <button onClick={this.btnClick}>Submit</button>
             {(this.state.todos.filter(Boolean).length > 0 )? TodoItems : <p>List is Empty</p>}  
            </div>
             )
        }
}

export default App