import React from 'react'
import close from "../assets/close.png"

function TodoItem(props){

    const completedStyle ={
        fontStyle: 'italic',
        textDecoration: 'Line-through',
        color:'grey'
    }

    return(
        // <li className='todo-item'><input type="checkbox"/><span>Attack On Titan</span></li>
        <div className='todo-item'>
            <input type="checkbox" checked={props.items.completed} onChange={()=> props.handleChange(props.items.id)}/>
            <p style ={props.items.completed ? completedStyle : null}>{props.items.text}</p>
            <img src={close} alt='..' width='20' height='20' style={{marginLeft: '40px'}} onClick={()=>props.handleDel(props.items.id)}/>
        </div>
    )
}

export default TodoItem